@font-face {
  font-family: 'Montserrat-Extrabold';
  src: local('Montserrat-Extrabold'),
  url('../assets/font/Montserrat-ExtraBold.otf')
}

@font-face {
  font-family: 'NoeDisplay-Bold';
  src: local('NoeDisplay-Bold'),
  url('../assets/font/NoeDisplay-Bold.ttf')
}

@font-face {
  font-family: 'Tuesday Night';
  font-display: block;
  src: local('Tuesday Night')
       url('../assets/font/TuesdayNight-Regular.otf') format('otf'), /* Super Modern Browsers */
       url('../assets/font/TuesdayNight-Regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('../assets/font/TuesdayNight-Regular.woff') format('woff'), /* Pretty Modern Browsers */
       url('../assets/font/TuesdayNight-Regular.ttf')  format('truetype'), /* Safari, Android, iOS */
       url('../assets/font/TuesdayNight-Regular.svg') format('svg'); /* Legacy iOS */
}

.NavText {
  font-family: 'Montserrat-Extrabold';
  float: right;
}

.BodyText {
  margin-top: 6vw;
  text-align: center;
  font-family: 'Montserrat-Extrabold';
  font-size: 3vw;
  -webkit-text-fill-color: red;
  display: inline;
}
.BodyTextBlack {
  margin-top: 6vw;
  text-align: center;
  font-family: 'Montserrat-Extrabold';
  font-size: 3vw;
  display: inline;
}

.TeusdayHeader {
  text-align: center;
  font-family: 'Tuesday Night';
  font-size: 15vw;
  background: linear-gradient(90deg, rgba(235,51,73,1) 0%, rgba(229,37,56,1) 58%, rgba(244,128,67,1) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  padding-top: 7vw;
}


